.main{
  overflow: hidden;
  position: relative;
  &__video{
    //min-height: 67.5em;
    height: 100%;
    position: relative;
    video{
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(0deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0.75) 100%);
    }
  }
}

.main-slider{
  position: absolute;
  border-radius: 0.3125rem;
  background: rgba(0, 0, 0, 0.40);
  backdrop-filter: blur(5px);
  height: 100%;
  width: 31em;
  top: 0;
  right: 0;
  z-index: 3;
  padding: 15em 3.13em 5em;
  .swiper{
    &-container{
      overflow: hidden;
    }
    &-slide{
      width: 100%;
    }
    &-nav{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
    }
    &-button{
      background: #101010;
      path{
        fill: #fff;
      }
    }
    &-pagination{
      margin-bottom: 2.5em;
    }
  }
}

.product-card{
  max-width: 22em;
  color: #fff;
  &__top{
    display: flex;
    align-items: center;
    margin-bottom: .8em;
  }
  &__badge{
    margin-left: 1.25em;
    @include center;
    padding: 0 1.25em;
    height: 2.5625em;
    background-color: $primary;
    color: $black;
  }
  &__img{
    position: relative;
    padding-bottom: 100%;
    img{
      @include centerAbsolute;
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__info{
    margin-top: 1.25em;
    @include grid100gap(.625em);
  }
}

.products{
  border-bottom: 1px solid #E7E7E7;
  position: relative;
  &._active{
    .products__hover{
      opacity: 1;
    }
  }
  @media screen and (min-width: 993px){
    &__hover{
      position: absolute;
      top: 0;
      left: calc(25% + 2.5em);
      width: calc(25% - 2.5em);
      height: 21.875em;
      background: rgba($secondary, .5);
      z-index: 3;
      opacity: 0;
      transition: left ease .6s,
                  top ease .6s,
                  width ease .75s,
                  height ease .75s,
                  opacity ease .3s;
      pointer-events: none;
    }
    &__top{
      position: absolute;
      top: 0;
      left: 0;
      width: 32.5em;
      height: 21.875em;
      background-image: url(../img/bg.webp);
      background-size: cover;
      color: #fff;
      padding: 3.13em 5em;
    }
    .swiper{
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: calc(25% + 2.5em) calc(25% - 2.5em) calc(25% - 2.5em) calc(25% + 2.5em);
        grid-auto-flow: dense;
        &:before{
          content: '';
        }
      }
      &-slide{
        &:nth-child(3),
        &:nth-child(4){
          .product-card2__img img{
            right: 50%;
            transform: translateX(50%);
          }
        }
        &:nth-child(3){
          grid-column: 4/5;
          grid-row: 1/3;
        }
        &:nth-child(4){
          grid-column: 1/2;
          grid-row: 2/4;
        }
        &:nth-child(7){
          grid-column: 2/4;
          .product-card2__img{
            img{
              max-width: 34.25em;
            }
          }
        }
      }
      &-pagination,
      &-nav{
        display: none;
      }
    }
  }
}

.products-slider{
  position: relative;
  z-index: 3;
}

.product-card2{
  position: relative;
  height: 100%;
  display: block;
  &:hover{
    color: $black;
    //z-index: 3;
    //background-color: transparent !important;
  }
  &._yellow{
    background-color: $primary;
  }
  &._gray{
    background-color: #F5F5F5;
  }
  &._blue{
    background-color: $secondary;
    color: #fff;
  }
  &__top{
    padding: 3.125em;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
  &__img{
    position: relative;
    min-height: 21.875em;
    height: 100%;
    img{
      position: absolute;
      max-width: calc(100% - 4.5em);
      max-height: calc(100% - 4.5em);
      bottom: 2.5em;
      right: 2.5em;
      display: block;
    }
  }
}

.collection{
  position: relative;
  overflow: hidden;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    top: 0;
    left: 0;
    padding: 6.25em 5em 0;
    width: 100%;
    z-index: 2;
  }
  .swiper{
    &-wrapper{
      align-self: stretch;
    }
    &-slide{
      width: 25%;
      height: auto;
      transition: all .3s;
      &:not(.swiper-slide-active){
        .product-card3{
          margin-left: -1px;
        }
      }
      &-prev{
        opacity: 0;
      }
    }
    &-nav{
      width: 25%;
      .swiper-button{
        opacity: 1;
      }
    }
    &-pagination{
      background: #E9E9E9;
      height: .3125em;
      border-radius: 0;
      position: absolute;
      bottom: 6.25em;
      left: 0;
      width: 100%;
      z-index: 3;
      top: unset;
      &-progressbar-fill{
        background: $primary;
        border-radius: 0;
        transition: all .3s;
      }
    }
  }
}

.collection-slider{
  width: calc(100% + 1px);
  transform: translateX(1px);
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 13.95em;
    left: 50%;
    width: calc(100% + 10em);
    height: 1px;
    transform: translateX(-50%);
    background-color: #E7E7E7;
  }
}

.product-card3{
  padding: 17em 3.12em 9.7em;
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  border-left: 1px solid #E7E7E7;
  border-right: 1px solid #E7E7E7;
  &:hover{
    color: $black;
    &:before{
      background-color: $primary;
    }
    .product-card3__title{
      color: $primary;
    }
    ._line{
      background-color: $primary;
    }
  }
  ._line{
    background-color: transparent;
    transition: all .3s;
    position: absolute;
    z-index: 2;
    top: 13.95em;
  }
  ._top-line{
    left: 0;
    width: 100%;
    height: 1px;
  }
  ._right-line,
  ._left-line{
    height: calc(100% - 20.2em);
    width: 1px;
  }
  ._left-line{
    left: 0;
    transform: translateX(-50%);
  }
  ._right-line{
    right: 0;
    transform: translateX(50%);
  }
  ._rect{
    content: '';
    position: absolute;
    right: 100%;
    top: 13.95em;
    width: 0.6875rem;
    height: 0.6875rem;
    background-color: $primary;
    transform: translate(50%,-50%);
    z-index: 2;
  }
  ._rect2{
    right: unset;
    left: 100%;
    transform: translate(-35%,-50%);
  }
  &__img{
    @include imgCover(106%);
  }
  &__info{
    margin-top: 1.25em;
    @include grid100gap(.625em);
  }
  &__title{
    transition: all .3s;
  }
  &__props{
    margin-top: auto;
    padding-top: 1.25em;
    @include grid100gap(.625em);
  }
  &__prop{
    display: flex;
    justify-content: space-between;
  }
}

.consult{
  display: grid;
  grid-template-columns: 50% 50%;
  &__media{
    //background-image: url(../img/consult-bg.svg);
    background-repeat: no-repeat;
    background-position: right center;
    background-size: calc(100% - 10.38em) auto;
    background-color: $primary;
    position: relative;
    svg{
      position: absolute;
      top: 50%;
      right: 0;
      transform: translateY(-50%);
      width: calc(100% - 10.38em);
      height: auto;
    }
    path{}
  }
}

.consult-form{
  position: relative;
  padding-bottom: 6.25em;
  &:before,
  &:after{
    content: '';
    position: absolute;
    pointer-events: none;
    height: 100%;
    top: 0;
    left: 0;
    border-right: 1px solid #E7E7E7;
    z-index: -1;
  }
  &:before{
    width: calc(50% + 2.5em);
    transform: translateX(1px);
  }
  &:after{
    width: 5em;
    transform: translateX(2px);
  }
  &__top{
    padding: 6.25em 0 3.125em 5em;
  }
  .fg{
    width: calc(50% + 2.5em);
    height: 5em;
    border-top: $border3;
    border-bottom: $border3;
    input{
      position: absolute;
      width: calc(100% - 5em);
      right: 0;
      top: 0;
      z-index: 2;
    }
    label{
      padding-left: 7.5em;
    }
    &:before{
      width: 0.6875em;
      height: 0.6875em;
      z-index: 2;
      top: 0;
      left: calc(5em + 1px);
    }
  }
  &__bottom{
    border-bottom: $border3;
    border-top: $border3;
    padding: 0 5em;
    margin-top: -1px;
    display: grid;
    grid-template-columns: calc(50% + 2.5em) auto;
  }
  &__confirm{
    padding-left: 3.19em;
    display: flex;
    align-items: center;
    a{
      color: $secondary;
      text-decoration: underline;
    }
  }
}

.work{
  padding: 6.25em 0;
  background: #FAFAFA;
  &__title{
    text-align: center;
  }
  &__row{
    display: flex;
    justify-content: space-between;
    margin-top: 3.125em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 8.3125em;
      left: 0;
      width: 100%;
      height: 1px;
      background: #C5C5C5;
    }
  }
  &__row1{
    &:after{
      content: '';
      position: absolute;
      height: calc(17.375em + 4.81em);
      width: 1px;
      background: #C5C5C5;
      right: 0;
      top: 8.3125em;
    }
    .work-card{
      &:first-child{
        &:after{
          content: '';
          position: absolute;
          width: 18.75em;
          height: 0.1875em;
          top: 8.19em;
          left: 0;
          background-color: $primary;
        }
      }
    }
  }
  &__row2{
    @media screen and (min-width: 993px){
      margin-top: 4.81em;
      flex-direction: row-reverse;
      justify-content: center;
      .work-card{
        margin: 0 7.19em;
      }
    }
  }
}

.work-card{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 27.0625em;
  height: 17.375em;
  &__num{
    color: #EDEDED;
    font-size: 6.25em;
    font-weight: 700;
    line-height: 140%;
  }
  &__title{
    margin-top: -3.625em;
  }
  &__text{
    max-width: 18.75em;
    width: 100%;
    margin-top: .7em;
  }
  &__rect{
    margin-top: .5em;
    width: 1.3125em;
    height: 1.3125em;
    background-color: $primary;
    position: relative;
    z-index: 2;
  }
  &__text{
    .text16{
      @include truncate(6);
    }
  }
}

.objects{
  padding-top: 6.25em;
  &__top{
    max-width: 42.8125em;
    margin-bottom: 3.125em;
  }
  .swiper{
    &-container{
      height: 100%;
    }
    &-slide{
      width: 100%;
    }
    &-pagination{
      position: absolute;
      right: 5em;
      left: unset;
      top: 50%;
      transform: translateY(-50%);
      z-index: 2;
      flex-direction: column;
      width: .9375em;
      &-bullet{
        width: 0.9375em;
        height: 0.9375em;
        background: #FFF;
        margin: .625em 0 !important;
        position: relative;
        &:before{
          content: '';
          @include centerAbsolute;
          width: 1.875em;
          height: 1.875em;
          border: 1px solid $primary;
          opacity: 0;
          transition: inherit;
        }
        &-active{
          background: $primary;
          &:before{
            opacity: 1;
          }
        }
      }
    }
    &-nav{
      width: 27.5em;
      position: absolute;
      right: 5em;
      bottom: 0;
    }
    &-button{
      background-color: $secondary;
      path{
        fill: #fff;
      }
    }
  }
}

.objects-slider{
  overflow: hidden;
  height: 50em;
  position: relative;
}

.object-card{
  position: relative;
  height: 100%;
  &__title{
    position: absolute;
    top: 0;
    left: 0;
    padding: 3.125em 5em;
    color: #fff;
  }
  &__img{
    height: 100%;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
}




@media screen and (max-width: 992px){
  .main{
    &__video{
      position: absolute;
      min-height: unset;
      width: 100%;
    }
  }

  .main-slider{
    position: static;
    width: 100%;
    min-height: $fullHeight;
    padding: 7em 1.25em 5em;
    .swiper{
      &-pagination{
        margin-bottom: 1.25em;
        &-bullet{
          width: 1.3em;
        }
      }
    }
  }

  .product-card{
    &__badge{
      height: 2.375em;
      span{
        font-size: .875em;
      }
    }
    &__info{
      gap: .31em;
      @include lastM(0 0 .31em);
    }
  }

  .products{
    overflow: hidden;
    border-bottom: none;
    &__top{
      padding: 3.125em 1em 1.875em;
      text-align: center;
    }
  }

  .products-slider{
    .swiper{
      &-container{
        position: relative;
      }
      &-button{
        width: 2.25em;
        height: 2.25em;
        background-color: $black;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        path{
          fill: #fff;
        }
        &-prev{
          left: 0;
        }
        &-next{
          right: 0;
        }
      }
      &-pagination{
        padding: 0 .9375em;
        flex-wrap: nowrap;
        margin-top: .63em;
        &-bullet{
          background: #DDD;
          flex-grow: 1;
          width: auto;
          margin: 0 .3125em !important;
          &-active{
            background: $primary;
          }
        }
      }
    }
  }

  .product-card2{
    &__top{
      padding: 1.88em;
      text-align: center;
      .text24{
        font-size: 1.25em;
      }
    }
    &__img{
      height: 15.875em;
      min-height: unset;
      img{
        max-height: calc(100% - 6.5em);
        right: 50%;
        transform: translateX(50%);
      }
    }
  }

  .collection{
    &__top{
      position: static;
      padding: 0;
    }
    &__title{
      position: absolute;
      padding: 3.12em 1.25em 0;
      width: 100%;
      top: 0;
      left: 0;
      z-index: 2;
    }
    .swiper{
      &-slide{
        width: 50%;
      }
      &-nav{
        position: absolute;
        left: 0;
        bottom: 2.5em;
        width: 100%;
        padding: 0 1.25em;
        z-index: 2;
      }
      &-scrollbar{
        bottom: 6.05em;
      }
    }
  }

  .collection-slider{
    &:before{
      top: 7.8em;
    }
  }

  .product-card3{
    padding: 9em 1.25em 7.625em;
    ._rect,
    ._top-line{
      top: 7.8em;
    }
    ._left-line,
    ._right-line{
      height: calc(100% - 13.8em);
      top: 7.8em;
    }
    &__info{
      margin-top: 1.125em;
      gap: .3125em;
      .text22{
        font-size: 1.125em;
      }
    }
    &__prop{
      width: calc(100% + 1.1em);
      @include grid100gap(.4em);
    }
  }

  .consult{
    grid-template-columns: 100%;
    position: relative;
    &:after{
      content: '';
      position: absolute;
      top: 0;
      right: 1.25em;
      height: 100%;
      width: 1px;
      background: #E7E7E7;
      transform: translateX(2px);
    }
    &__media{
      display: none;
    }
  }

  .consult-form{
    padding-bottom: 3.125em;
    &:before{
      width: 50%;
    }
    &:after{
      width: 1.25em;
    }
    &__top{
      padding: 3.125em 1.25em 1.875em;
    }
    .fg{
      width: 50%;
      &:before{
        left: 1.25em;
      }
      input{
        width: calc(100% - 1.25em);
        padding-left: 1.87em;
        padding-right: 1.87em;
      }
      label{
        padding-left: 3.125em;
      }
    }
    &__bottom{
      padding-left: 1.25em;
      padding-right: 1.25em;
      grid-template-columns: 50% auto;
    }
    &__confirm{
      padding-left: 1.25em;
    }
  }

  .work{
    padding: 3.125em 0;
    position: relative;
    &__title{
      position: relative;
      z-index: 3;
    }
    &:before,
    &:after{
      content: '';
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(calc(-50% + 1px));
    }
    &:before{
      background: #C5C5C5;
      width: 1px;
      height: 100%;
    }
    &:after{
      z-index: 2;
      background: $primary;
      width: .1875em;
      height: 26.125em;
    }
    &__row{
      z-index: 3;
      margin-top: 1.875em;
      @include grid100gap(1.875em);
      &:before,
      &:after{
        display: none;
      }
    }
    &__row1{
      .work-card{
        &:first-child{
          &:after{
            display: none;
          }
        }
      }
    }
  }

  .work-card{
    max-width: 100%;
    margin: 0 auto;
    height: auto;
    &__text{
      max-width: 100%;
    }
  }

  .objects{
    padding-top: 3.125em;
    &__top{
      margin-bottom: 1.875em;
      text-align: center;
    }
    .swiper{
      &-pagination{
        display: none;
      }
      &-nav{
        position: static;
        width: 100%;
      }
    }
  }

  .objects-slider{
    height: unset;
  }

  .object-card{
    &__title{
      display: none;
    }
    &__img{
      padding-bottom: 60%;
      img{
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .collection{
    &__title{
      text-align: center;
    }
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }

  .product-card3{
    padding-bottom: 7em;
    &__props{
      margin-top: 0;
    }
  }

  .consult-form{
    &:before{
      display: none;
    }
    &__top{
      text-align: center;
    }
    .fg{
      width: 100%;
      &:before,
      &:after{
        width: 0.3125em;
        height: 0.3125em;
      }
      &:after{
        content: '';
        position: absolute;
        right: 1.25em;
        top: 0;
        background-color: $primary;
        transform: translate(50%,-50%);
      }
      input{
        right: 1.25em;
        width: calc(100% - 2.5em);
      }
    }
    &__bottom{
      grid-template-columns: 100%;
      gap: .625em;
      border: none;
      padding: 0;
      z-index: 3;
      position: relative;
    }
    &__confirm{
      padding: 0 1.25em;
      text-align: center;
      .text14{
        font-size: .75em;
      }
    }
  }

  .object-card{
    &__img{
      padding-bottom: 140%;
    }
  }
}
