.header{
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  display: flex;
  color: #fff;
  &:before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    border-bottom: $border;
    z-index: 11;
  }
  &__logo{
    display: block;
    height: 6.25em;
    padding: 0 3.12em;
    width: 15.06em;
    flex-shrink: 0;
    @include center;
    border-right: $border;
    img{
      width: 100%;
      display: block;
    }
    ._dark,
    ._view2{
      display: none;
    }
  }
  &__logo2{
    width: 13.25em;
    ._dark{
      display: none;
    }
  }
  &__right{
    margin-left: auto;
    display: flex;
    align-items: center;
  }
  &._black:not(._open){
    color: $black;
    position: relative;
    .mob-menu{
      &__btn{
        path{
          fill: $black;
        }
      }
    }
    .header__logo,
    &:before,
    .burger, .mob-menu__btn, .mob-menu__link, .mob-menu__phone{
      border-color: #E7E7E7;
    }
    .burger:hover .burger__icon{
      path{
        fill: $black;
      }
    }
    .burger__icon ._view1{
      path{
        fill: $primary;
      }
    }
    .header__logo{
      ._dark{
        display: block;
      }
      ._light{
        display: none;
      }
    }
  }
  &._open{
    .catalog{
      opacity: 1;
      pointer-events: unset;
      transform: translateY(0);
    }
  }

}

._rect-after,
._rect-before{
  position: relative;
  &:before{
    content: '';
    left: 100%;
    top: 100%;
    transform: translate(-50%,-50%);
  }
  &:after{
    right: 100%;
    top: 100%;
    transform: translate(50%,-50%);
  }
  &:after,
  &:before{
    background-color: $primary;
    width: .3125em;
    height: .3125em;
    z-index: 2;
    position: absolute;
  }
}

._rect-after{
  &:after{
    content: '';
  }
}

.burger,
.mob-menu__link,
.mob-menu__phone,
.mob-menu__btn{
  height: 6.25em;
  @include center;
  border-left: $border;
}

.burger,
.mob-menu__link{
  padding: 0 3.12em;
  text-transform: uppercase;
}

.mob-menu{
  display: flex;
  &__top{
    width: 31em;
    display: grid;
    grid-template-columns: calc(100% - 15.5em) 7.75em 7.75em;
  }
  &__btn{
    svg{
      width: 1.5em;
      height: auto;
    }
    &:hover{
      path{
        fill: $primary;
      }
    }
  }

}

.burger:hover,
.header._open .burger{
  color: $black;
  background-color: $primary;
  border-color: $primary;
  path{
    fill: $black;
  }
}

.header._open .burger{
  ._view1{
    opacity: 0;
  }
  ._view2{
    opacity: 1;
  }
}

.burger{
  cursor: pointer;
  transition: all .3s;
  &__icon{
    width: 1.5em;
    height: 1.5em;
    flex-shrink: 0;
    position: relative;
    margin-right: 1.25em;
    svg{
      width: 100%;
      height: auto;
      @include centerAbsolute;
    }
    ._view2{
      opacity: 0;
    }
  }
}

.header__logo,
.mob-menu__top,
.burger,
.mob-menu__link{
  position: relative;
  z-index: 11;
}

.catalog{
  opacity: 0;
  pointer-events: none;
  transition: all .4s;
  transform: translateY(-150%);
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: $fullHeight;
  background-color: rgba(#111,.9);
  z-index: 10;
  padding-top: 6.25em;
  overflow-y: auto;
  display: grid;
  grid-template-columns: 43.2% 56.8%;
  &__back{
    display: none;
  }
  &__tab{
    padding: 0 4.0625em;
    height: 7.6875em;
    width: calc(100% + 1px);
    display: flex;
    align-items: center;
    text-transform: uppercase;
    cursor: pointer;
    transition: all .3s;
    border-top: $border2;
    position: relative;
    &:last-child{
      border-bottom: $border2;
    }
    &:before{
      content: '';
      left: 100%;
      top: 0;
      transform: translate(-50%,-50%);
      background-color: $primary;
      width: .6875em;
      height: .6875em;
      z-index: 2;
      position: absolute;
    }
    .text20{
      letter-spacing: 0.1125em;
    }
    &:hover,
    &._active{
      background-color: $primary;
      color: $black;
      border-color: $primary !important;
    }
    &._active{
      pointer-events: none;
    }
  }
  .tab-content{
    padding: 3.38em 3.7em 7em 5em;
    width: 100%;
    height: 100%;
    position: relative;
  }
  &__left{
    border-right: $border2;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    text-transform: uppercase;
    @include lastM(0 0 3.125em);
    span{
      letter-spacing: 0.1em;
    }
  }
  &__img{
    position: absolute;
    bottom: 7em;
    right: 3em;
    width: calc(100% - 7em);
    height: calc(100% - 15em);
    display: flex;
    align-items: flex-end;
    justify-content: center;
    img{
      max-height: 100%;
      max-width: 100%;
      display: block;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}


@media screen and (max-width: 992px){
  .header{
    justify-content: space-between;
    transition: all .3s;
    &__logo{
      height: 5em;
      width: 4.88em;
      padding: 0 1.25em;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__logo1{
      ._dark,
      ._light{
        display: none !important;
      }
    }
    &__logo2{
      width: calc(100% - 9.76em);
      padding: .87em 1.66em;
      img{
        height: 100%;
        width: auto;
      }
    }
    &__right{
      margin: 0;
    }
    &._open{
      background: #1C1C1C;
      .mob-menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .burger{
    padding: 0;
    height: 5em;
    width: 4.88em;
    &:before{
      display: none;
    }
    &__icon{
      margin: 0;
      width: 1.875em;
    }
    &__text{
      display: none;
    }
  }

  .mob-menu{
    position: fixed;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    background: #1C1C1C;
    width: 100%;
    height: calc(100 * var(--vh) - 3.75em);
    overflow-y: auto;
    left: 0;
    top: 5em;
    display: flex;
    flex-direction: column;
    &._fix{
      overflow: hidden;
    }
    &__top{
      position: sticky;
      top: 0;
      border-bottom: $border;
      background: #1C1C1C;
      z-index: 10;
      width: 100%;
      order: -1;
      display: grid;
      grid-template-columns: 4.88em calc(100% - 9.76em) 4.88em;
    }
    ._search{
      order: -1;
      border-left: none;
    }
    &__btn,
    &__phone{
      height: 3.75em;
    }
    &__phone{
      .text16{
        font-size: .875em;
      }
    }
    &__link{
      border-left: none;
      padding: 0 1.25em;
      justify-content: unset;
      &:before{
        display: none;
      }
    }
  }

  .catalog{
    display: block;
    position: static;
    pointer-events: unset;
    transform: unset;
    opacity: 1;
    height: unset;
    overflow: unset;
    padding: 0;
    background: transparent;
    &__tab{
      padding: 0 1.25em;
      height: 3.7em;
      &._active{
        background-color: transparent;
        color: #fff;
        border-color: #2B2B2B !important;
        pointer-events: unset;
      }
      &:before{
        display: none;
      }
      .text20{
        font-size: 1em;
      }
    }
    .tab-content{
      position: absolute;
      height: calc(100 * var(--vh) - 8.75em);
      top: 0;
      left: 0;
      width: 100%;
      background: #1C1C1C;
      overflow-y: auto;
      padding: 0 0 2em;
      opacity: 0;
      pointer-events: none;
      transition: all .3s;
      &._active{
        display: flex;
        flex-direction: column;
      }
      &._active2{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &__back{
      display: flex;
      align-items: center;
      padding: 1.3em 1.25em 1.8em;
      font-weight: 700;
      color: $primary;
      border-bottom: $border2;
      width: 100%;
      span{
        font-size: 1em;
        letter-spacing: 0.1em;
        text-transform: uppercase;
      }
      img{
        width: 1.5em;
        flex-shrink: 0;
        margin-right: 1.3em;
      }
    }
    &__links{
      padding: 2em 1.25em 0;
    }
    &__img{
      position: static;
      width: 100%;
      height: calc(100% - 9em);
      margin-top: auto;
      padding-top: 1.7em;
    }
  }
}
