@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-primary{
    height: 5em;
    text-transform: uppercase;
    background-color: $primary;
    color: $black;
    font-weight: 600;
    span{
        font-size: 1em;
        line-height: 140%;
        letter-spacing: 0.1em;
    }
    &._gray:not(:hover){
        background: #ECECEC;
    }
    &:hover{
        background-color: $primaryHover;
        color: $black;
    }
}

.favourite-btn{
    width: 5.25em;
    height: 5.25em;
    &._active{
        ._view1{
            opacity: 0;
        }
        ._view2{
            opacity: 1;
        }
    }
    &__icon{
        height: 100%;
        width: 100%;
        position: relative;
        display: block;
        svg{
            @include centerAbsolute;
            width: 1.5em;
            height: auto;
        }
        ._view1:hover{
            path{
                fill: $primary;
            }
        }
        ._view2{
            opacity: 0;
        }
    }
}


@media screen and (max-width: 992px){
    .favourite-btn{
        width: 3em;
        height: 3em;
    }
}
