.text52{
  font-size: 3.25em;
  line-height: 140%;
}

.text28{
  font-size: 1.75em;
  line-height: 140%;
}

.text h3,
.text24{
  font-size: 1.5em;
  line-height: 121%;
}

.text h3{
  font-weight: 500;
}

.text22{
  font-size: 1.375em;
  line-height: 140%;
}

.text20{
  font-size: 1.25em;
  line-height: 130%;
}

.text p,
.text16{
  font-size: 1em;
  line-height: 140%;
}

.text14{
  font-size: .875em;
  line-height: 140%;
}

.text12{
  font-size: .75em;
  line-height: 140%;
}

.medium-text{
  font-weight: 500;
}

.semi-bold-text{
  font-weight: 600;
}

.bold-text{
  font-weight: 700;
}

.primary-text{
  color: $primary;
}

.gray-text{
  color: $gray;
}

.gray-text2{
  color: $gray2;
}

.strikethrough-text{
  text-decoration-line: strikethrough;
}

.text ul,
.m-list{
  @include grid100gap(1.25em);
  li{
    font-size: 1em;
    line-height: 120%;
    display: flex;
    &:before{
      content: '';
      flex-shrink: 0;
      width: .3125em;
      height: .3125em;
      background-color: $black;
      margin-right: .625em;
      transform: translateY(.375em);
    }
  }
}


@media screen and (max-width: 992px){
  .text52{
    font-size: 2em;
  }
}
