@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper{
  &-nav{
   display: grid;
   grid-template-columns: 50% 50%;
   .swiper-button{
     position: static;
     width: 100%;
     height: 3.75em;
     background: #ECECEC;
     margin: 0;
     svg{
       width: 1.5em;
       height: auto;
     }
     &:hover{
       background: $primary;
       path{
         fill: $black;
       }
     }
   }
 }
  &-pagination{
    position: static;
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + .31em);
    &-bullet{
      background: #2D2D2D;
      opacity: 1;
      width: 1.72em;
      height: 0.1875em;
      border-radius: 0.0625em;
      margin: .25em .31em 0 0 !important;
      &:hover,
      &-active{
        background: #D9D9D9;
      }
    }
  }
}


