.footer{
  background-color: $secondary;
  background-image: url(../img/bg2.svg);
  background-repeat: no-repeat;
  background-size: 40.9375em auto;
  background-position: 0 0;
  position: relative;
  color: #fff;
  &:before,
  &:after{
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 1px;
    background: #2F77BE;
  }
  &:before{
    top: 3.125em;
  }
  &:after{
    bottom: 3.125em;
  }
  &__inner{
    display: grid;
    grid-template-columns: repeat(4,25%);
  }
  &__col{
    padding: 6.25em 0;
    border-right: 1px solid #2F77BE;
    display: flex;
    flex-direction: column;
    &:before,
    &:after{
      width: 0.6875em;
      height: 0.6875em;
    }
    &:before{
      top: 3.125em;
      transform: translate(-50%,-50%);
    }
    &:after{
      top: unset;
      bottom: 3.125em;
      right: unset;
      left: 100%;
      transform: translate(-50%,50%);
    }
  }
  &__col1{
    grid-column: 1/3;
  }
  &__col2,
  &__col3{
    padding-left: 3.125em;
    padding-right: 3.125em;
  }
  &__logos{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__logo1{
    width: 10.625em;
  }
  &__logo2{
    width: 7.4375em;
    margin-left: 4.7em;
  }
  &__by{
    display: inline-flex;
    align-items: center;
    margin-top: auto;
    padding-top: 1.5em;
    color: #fff;
    svg{
      width: 2.5em;
      height: auto;
      margin-right: 1em;
    }
    span{
      font-size: .75em;
      line-height: 150%;
    }
  }
}


.footer-group{
  @include lastM(0 0 6.625em);
  &__show{
    text-transform: uppercase;
    img{
      display: none;
    }
  }
  &__hidden{
    margin-top: 1.25em;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    @include lastM(0 0 1.25em);
    .text16{
      line-height: 140%;
    }
  }
}

.social{
  display: flex;
  &__item{
    width: 3.875em;
    height: 3.875em;
    @include center;
    background: #105BA6;
    @include lastM(0 .3125em);
    img{
      width: 1.375em;
      display: block;
    }
    &:hover{
      background: $primary;
    }
  }
}

.footer-contacts{
  @include grid100gap(.625em);
  .text16{
    line-height: 140%;
  }
}


@media screen and (max-width: 992px){
  .footer{
    background-image: url(../img/bg3.svg);
    background-position: left 0 bottom 0;
    background-size: 100% auto;
    padding-bottom: 9.875em;
    &:before,
    &:after{
      height: 100%;
      width: 1px;
    }
    &:before{
      left: 1.25em;
      top: 0;
      transform: translateX(1px);
    }
    &:after{
      left: unset;
      right: 1.25em;
      bottom: 0;
      transform: translateX(2px);
    }
    .container{
      padding: 0;
    }
    &__inner{
      grid-template-columns: 100%;
    }
    &__col{
      padding: 0;
      border-right: unset;
      border-bottom: 1px solid #2f77be;
      &:before,
      &:after{
        width: 0.3125em;
        height: 0.3125em;
      }
      &:before{
        top: 100%;
        left: 1.25em;
        margin-left: 1px;
      }
      &:after{
        bottom: unset;
        top: 100%;
        left: unset;
        right: 1.25em;
        transform: translate(50%,-50%);
        margin-right: -1px;
      }
    }
    &__col1{
      grid-column: 1/-1;
      padding: 3.562em 2.5em 3.125em;
    }
    &__by{
      svg{
        width: 2em;
      }
      span{
        font-size: .75em;
      }
    }
  }

  .footer-group{
    padding: 1.25em 2.5em;
    position: relative;
    &._active{
      .footer-group__show img{
        transform: rotate(-180deg);
      }
    }
    &:not(:last-child){
      margin-bottom: 0;
      border-bottom: 1px solid #2F77BE;
      &:before,
      &:after{
        content: '';
        position: absolute;
        top: 100%;
        width: 0.3125em;
        height: 0.3125em;
        background-color: $primaryHover;
        z-index: 2;
      }
      &:before{
        left: 1.25em;
        transform: translate(-50%,-50%);
        margin-left: 1px;
      }
      &:after{
        right: 1.25em;
        transform: translate(50%,-50%);
        margin-right: -1px;
      }
    }
    &__show{
      display: flex;
      align-items: center;
      justify-content: space-between;
      .text14{
        font-size: 1.125em;
      }
      img{
        display: block;
        width: 1.5em;
        flex-shrink: 0;
        margin-left: 1em;
        transition: all .4s;
      }
    }
    &__hidden{
      display: none;
      margin-top: .625em;
    }
    &__link{
      @include lastM(0 0 .625em);
      .text16{
        font-size: .875em;
      }
    }
  }

  .footer-contacts{
    .text16{
      font-size: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__logo1{
      width: 6.3125em;
    }
    &__logo2{
      width: 6.125em;
      margin-left: 2.56em;
    }
  }
}
