@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $black;
    //font-size: calc(1920px * (16 / 1920));
    leading-trim: both;
    text-edge: cap;
    //@media screen and (max-width: 1920px){
    //    font-size: calc(100vw * (16 / 1920));
    //}
    font-size: calc(100vw * (16 / 1920));
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
    }
}

.container {
    width: 100%;
    margin: 0 auto;
    padding: 0 5em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .page{
        font-size: calc(100vw * .0208333);
    }

    .container{
        padding: 0 1.25em;
    }
}


@media screen and (max-width: 576px){
    .page{
        font-size: calc(100vw * .05);
    }
}
