.fg {
  position: relative;
  margin: 0;
  input {
    border: none;
    border-bottom: $border3;
    padding: 2.38em 2.5em 0;
    width: 100%;
    font-size: 1em;
    height: 5em;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: #000;
    }
    &:focus {
      border-color: $primary;
    }
    &.input-active,
    &:focus{
      &+label{
        transform: translateY(0%);
        top: 1.125em;
        span{
          font-size: .75em;
        }
      }
    }
  }
  label{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    padding: 0 2.5em;
    color: #717171;
    transition: all .1s;
    span{
      transition: inherit;
      font-size: 1em;
      line-height: 140%;
    }
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}
