.page-top{
    padding: 1.875em 0;
    &__inner{
        @include grid100gap(.3125em);
    }
}

.breadcrumbs {
    &__item {
        font-weight: 700;
        font-size: .75em;
        line-height: 125%;
        position: relative;
        display: inline;
        text-transform: uppercase;
        color: #C8C8C8;
        &:not(:first-child) {
            margin-left: 2.19em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: .42em;
                height: .42em;
                background-color: #C8C8C8;
                top: .3em;
                right: 100%;
                margin-right: 1em;
            }
        }
        &:last-child{
            color: $black;
            &:before{
                background-color: $primary;
            }
        }
    }
}


@media screen and (max-width: 992px){
    .page-top{
        padding: 1em 0;
    }
}
